@import "constantMixins";

.contact-page{
    padding-top: 1px;
    margin-top: -1px;
    @include default_page_styles_mixin;

    h1{@include default_title_heading_styles_mixin;margin-top: 18vh;}

    .contact-boxes{
        margin-left: auto;
        margin-right: auto;
        display: flex;
        justify-content: center;
        width: 90vw;
        margin-top: 5vh;
        flex-wrap: wrap;
    }
}

.ContactCard-card{
    height: 289px;
    width: 272px;
    border-radius: 23px;
    box-shadow: $large-card-box-shadow;
    background-color: $darker-bg-white;
    margin-right: $TabMob-margin + $TabMob-margin + $TabMob-margin + $TabMob-margin;

    .contact-card-img{
        margin-top: 58px;
        margin-left: 111px;
        margin-right: 111px;
    }

    h2{
        margin-top: 76px;
        font-size: 18px;
        font-weight: 600;
        color: $white-text-col;
        text-align: center;
    }

    .contact-link-btn{
        height: 48px;
        width: 258px;
        margin-left: $margin-val;
        margin-top: $margin-val + 15px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #BFA8FF;
        border-radius: 18px;
        cursor: pointer;

        h3{
            margin-right: 2px; 
            color: $btn-colours;
            font-size: 18px;
            font-weight: 600;
        }
    }
}

@media only screen and (min-width: 914px) and (max-width: 1200px){
    .contact-page{
        .contact-boxes{
            width: 95vw;
        }
    }

    .ContactCard-card{
        margin-top: $TabMob-margin + $TabMob-margin + $TabMob-margin + $TabMob-margin;
        margin-right: $TabMob-margin; 
        margin-left: $TabMob-margin;
    }
}

@media only screen and (min-width: 600px) and (max-width: 914px){
    .contact-page{
        height: 90vh;
        h1{margin-top: 8vh;}
    }

    .ContactCard-card{
        margin-top: $TabMob-margin + $TabMob-margin + $TabMob-margin + $TabMob-margin;
        margin-right: $TabMob-margin; 
        margin-left: $TabMob-margin;
    }
}

@media only screen and (max-width: 600px){
    .contact-page{
        h1 {margin-top: 10vh;}
        .contact-boxes{flex-direction: column;  margin-top: -2vh; justify-content: center;}
    }

    .ContactCard-card{
        margin-top: $TabMob-margin + $TabMob-margin + $TabMob-margin;
        margin-left: auto;
        margin-right: auto;
        height: 178px;
        .contact-card-img{margin-top: 9px;}
        h2{margin-top: 5px;}

        .contact-link-btn{
            margin-top: $margin-val + 24px;
        }
    }
}

@media only screen and (max-width: 600px) and (max-height: 850px){
    .contact-page{
        height: 110vh;
    }
}

@media only screen and (max-width: 600px) and (max-height: 750px){
    .contact-page{
        height: 140vh;
    }
}