@import "constantMixins";

.projects-page{
    padding-top: 1px;
    margin-top: -1px;
    @include default_page_styles_mixin;

    .page-heading{
        display: flex;
        margin-top: 10vh;
        justify-content: center;
        h1{font-size: 42px; margin-right: 10px;color: $TT-heading-col;}
        .projects-shape{margin-left: 10px;}
    }

    .projects-slider{
        height: 70vh;
        width: 97vw;
        display: flex;
        align-items: center;

        .left-slider{
            margin-left: $icon-margin-sep;
            cursor: pointer;
            .squricle-shape{@include squircle_mixin;}
            .leftslider-shape{@include squircle_icon_mixin_bigger;}
        }

        .swiper-frame{
            margin-left: $icon-margin-sep;
            height: 70vh;
            width: 85vw;
            .swiper-slide{
                margin-top: 3vh;
                height: 63vh;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }

        .right-slider{
            cursor: pointer;
            .squricle-shape{@include squircle_mixin;}
            .rightslider-shape{@include squircle_icon_mixin_bigger;}
        }
    }
}

.slider-card{
    height: 368px;
    width: 272px;
    border-radius: 18px;
    box-shadow: $large-card-box-shadow;
    background-color: $darker-bg-white;

    .repo-card-img{ border-radius: 18px;}

    .project-img-container{
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
        border-top-left-radius: 18px;
        border-top-right-radius: 18px;
        width: 272px;
        height: 165px;
        display: flex;
        justify-content: center;
        align-items: center;

        .bg-card-img{
            border-bottom-left-radius: 5px;
            border-bottom-right-radius: 5px;
            border-top-left-radius: 18px;
            border-top-right-radius: 18px;
        }

        .card-img{
            box-shadow: 4px 4px 10px 3px rgba(0, 0, 0, 0.25);
            border-radius: 5px;
        }

        .img-holder{
            position: absolute;
            border-bottom-left-radius: 5px;
            border-bottom-right-radius: 5px;
            border-top-left-radius: 18px;
            border-top-right-radius: 18px;
            width: 272px;
            height: 165px;
            display: flex;
            justify-content: center;
            align-items: center;   
        }
    }

    

    .card-text, .bigger-card-text{
        color: $white-text-col;
        height: 132px;
        width: 258px;
        font-size: 18px;
        font-weight: 400;
        margin-left: $margin-val;
        margin-top: 8px - $margin-val;
        text-align: justify;
    }

    .bigger-card-text{height: 38px; text-align: center; margin-top: 3px - $margin-val;}

    .repo-link-btn{
        height: 48px;
        width: 258px;
        margin-left: $margin-val;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #65AD59;
        border-radius: 14px;
        cursor: pointer;

        h3{
            margin-right: 2px; 
            color: $btn-colours;
            font-size: 18px;
            font-weight: 400;
        }

        .gh-repo-lnk{margin-left: 2px;}
    }
}

@media only screen and (min-width: 914px) and (max-width: 1200px){
    .projects-page{
    
        .projects-slider{
    
            .left-slider{
                margin-left: 3px;
            }
    
            .swiper-frame{
                margin-left: 0px;
                width: 95vw;
            }
        }
    }
}

@media only screen and (min-width: 600px) and (max-width: 914px){
    .projects-page{
        .page-heading{
            margin-top: 3vh;
        }
    
        .projects-slider{
            width: 100vw;
    
            .left-slider, .right-slider{display: none;}
    
            .swiper-frame{
                margin-left: 0px;
                width: 100vw;
            }
        }
    }
}

@media only screen and (min-width: 600px) and (max-width: 713px){
    .projects-page{
        height: 70vh;
        .page-heading{
            margin-top: 3vh;
        }
    
        .projects-slider{
            height: 50vh;
            width: 100vw;
    
            .left-slider, .right-slider{display: none;}
    
            .swiper-frame{
                margin-left: 0px;
                width: 100vw;
                height: 50vh;
                .swiper-slide{
                    height: 43vh;
                }
            }
        }
    }
}

@media only screen and (max-width: 600px){
    .projects-page{
        .page-heading{
            margin-top: 3vh;
        }
    
        .projects-slider{
            width: 100vw;
    
            .left-slider, .right-slider{display: none;}
    
            .swiper-frame{
                margin-left: 0px;
                width: 100vw;
            }
        }
    }
}