@import "constantMixins";

.skills-page{
    padding-top: 1px;
    margin-top: -1px;
    @include default_page_styles_mixin;
    height: 165vh;

    h1{@include default_title_heading_styles_mixin;}

    .skills-page-main-content{
        width: 90vw;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        flex-wrap: wrap;

        .programming-languages{
            width: 35vw;
            display: flex;
            flex-wrap: wrap;
            margin-left: 5vw;
            margin-top: 10px;
        }

        .education-job-experience{
            width: 50vw;
            display: flex;
            flex-wrap: wrap;
            margin-top: 10px;
            justify-content: center;
        }

        .dev-environments{
            width: 90vw;
            display: flex;
            margin-top: 3vh;
            align-items: center;
            justify-content: center;
            flex-wrap: wrap;
            min-height: 20vh;
            h3{
                text-align: center;
                font-size: 20px;
                font-weight: 600;
                margin-top: $margin-val;
                color: $white-text-col;
            }
            .environments-icons{margin-top: -2vh;display: flex;}
        }

        .operating-systems{
            width: 90vw;
            display: flex;
            margin-top: 3vh;
            align-items: center;
            justify-content: center;
            flex-wrap: wrap;
            min-height: 20vh;
            h3{
                text-align: center;
                font-size: 20px;
                font-weight: 600;
                margin-top: $margin-val;
                color: $white-text-col;
            }
            .operating-systems-icons{margin-top: -2vh;display: flex;}
        }
    }
}

.TechIcon-card{
    margin-left: $TabMob-margin + $TabMob-margin;
    margin-top: $TabMob-margin + $TabMob-margin;
    display: flex;
    flex-direction: column;
    align-items: center;
    .tech-icon-box{
        width: 50px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        .squricle-shape{filter: var(--squircle-drop-shadow);}
        .techicon-shape-container{
            position: absolute;
            width: 50px;
            height: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        
    }

    h2{
        text-align: center;
        font-size: 16px;
        font-weight: 500;
        margin-top: $margin-val;
        color: $white-text-col;
    }
}

.education-work-card{
    margin-left: $TabMob-margin + $TabMob-margin + $TabMob-margin + $TabMob-margin;
    margin-top: $TabMob-margin + $TabMob-margin;
    border-radius: 23px;
    box-shadow: $card-box-shadow;
    background-color: $darker-bg-white;
    width: 199px;
    height: 158px;
    display: flex;
    flex-direction: column; 
    flex-wrap: wrap;        
    justify-content: center;
    align-items: center;

    h1, h2, h3{
        font-size: 18px;
        font-weight: 800;
        margin-top: 2px;
        color: $white-text-col;
        text-align: center;
    }

    h2{font-size: 16px;
        font-weight: 500;}

    h3{font-size: 14px; font-weight: 400;}
}

@media only screen and (min-width: 914px) and (max-width: 1200px){
    .skills-page{
        .skills-page-main-content{
            width: 95vw;
            .programming-languages{
                width: 40vw;
                margin-top: -2vh;
            }

            .education-job-experience{
                margin-top: -2vh;
            }
        }
    }
}

@media only screen and (min-width: 600px) and (max-width: 914px){
    .skills-page{ 
        .skills-page-main-content{
            flex-direction: column;
            justify-content: center;

            .programming-languages{
                width: 50vw;
                margin-top: 0vh;
                margin-left: auto;
                margin-right: auto;
                justify-content: center;
    
            }

            .education-job-experience{
                width: 90vw;
                margin-top: 2vh;
            }

            .dev-environments{margin-top: 2vh;}
        }
    }

    .TechIcon-card{
        margin-left: $TabMob-margin;
        margin-right: $TabMob-margin;
    }

    .education-work-card{
        margin-left: $TabMob-margin + $TabMob-margin;
        margin-right: $TabMob-margin + $TabMob-margin;
    }
}

@media only screen and (max-width: 600px){
    
    .skills-page{ 
        height: 260vh;
        .skills-page-main-content{
            flex-direction: column;
            justify-content: center;

            .programming-languages{
                width: 90vw;
                margin-top: 0vh;
                margin-left: auto;
                margin-right: auto;
                justify-content: center;
    
            }

            .education-job-experience{
                width: 90vw;
                margin-top: 5vh;
                flex-direction: column;
            }

            .dev-environments{
                margin-top: 15vh;
                .environments-icons{
                    width: 90vw;
                    flex-wrap: wrap;
                    justify-content: center;
                }
            }

            .operating-systems{
                margin-top: 15vh;
                .operating-systems-icons{width: 90vw;
                    flex-wrap: wrap;
                    justify-content: center;}
            }
        }
    }

    .TechIcon-card{
        margin-left: $TabMob-margin;
        margin-right: $TabMob-margin;
    }

    .education-work-card{
        margin-left: auto;
        margin-right: auto;
    }
}

@media only screen and (max-width: 600px) and (max-height: 850px){
    .skills-page{
        height: 270vh;
    }
}

@media only screen and (max-width: 600px) and (max-height: 750px){
    .skills-page{
        height: 320vh;
    }
}