:root{
    --background-col: #E7E7E7;
    --squircle-drop-shadow: drop-shadow(15px 15px 15px rgba(0, 0, 0, 0.257));
    --lwb25per: #edeaea40;
    --blur-background: rgba(237, 234, 234, 0.25);
    --links-hover-col: #FF0000;
    --card-BS: 7px 15px 20px rgba(0, 0, 0, 0.257), -5px -5px 15px #ffffff;
    --large-card-BS: 7px 15px 20px rgba(0, 0, 0, 0.257), -5px -5px 15px #ffffff;
    --darker-bg-col: #FFFDFD;
    --pure-w-b: black;
    --border-img-col: #FF0000;

    --title-heading-text-col: #373737;
}

[data-theme='dark'] {
    --background-col: #2C2C2C;
    --darker-bg-col: #2C2C2C;
    --squircle-drop-shadow: drop-shadow(15px 15px 15px rgba(0, 0, 0, 0.6));
    --lwb25per: #63636340;
    --blur-background: rgba(237, 234, 234, 0.05);
    --links-hover-col: #ff5050;
    --card-BS: 10px 10px 20px #121212b2, -3px -3px 15px #383838;
    --large-card-BS: 10px 10px 25px #121212b2, -3px -3px 20px #383838;
    --pure-w-b: white;
    --border-img-col: #2C2C2C;

    --title-heading-text-col: white;
}

//colours
$dark-red: var(--links-hover-col);
$dark-red-img-border: var(--border-img-col);
$dark-red-selector: #FE3838;
$light-red-selector: #FFA5A5;
$white-text-col: var(--pure-w-b);
$light-gray-text: #747474;
$light-red-text: #BC5B5B;
$light-white-blur-25percent: var(--lwb25per);
$darker-bg-white: var(--darker-bg-col);
$background-col: var(--background-col);
$blur: var(--blur-background);
$TT-heading-col: var(--title-heading-text-col);

$btn-colours: #373737;

//sizing and spacing
$TabMob-margin: 15px;
$margin-val: 7px;
$font-size-small: 1.1rem;
$font-size-smallest: 0.9rem;
$font-size-medium: 1.7rem;
$font-size-large: 2.2rem;
$icon-margin-sep: 3vh;

//transition times

//z-index
$stack-ontop-z-index: 1000;
$stack-below-z-index: 500;

$card-box-shadow: var(--card-BS);
$large-card-box-shadow: var(--large-card-BS);

@mixin squircle_mixin {
    margin-top: $margin-val;
    margin-left: $margin-val;
    filter: var(--squircle-drop-shadow);
}

@mixin squircle_icon_mixin {
    position: absolute;
    margin-left: -36px;
    margin-top: 20px;
}

@mixin squircle_icon_mixin_bigger{
    position: absolute;
    margin-top: 7px;
    margin-left: -50px;
}

@mixin squircle_icon_mixin_big{
    position: absolute;
    margin-top: 11px;
    margin-left: -45px;
}

@mixin blob_objects_mixin {
    width: 500px;
    height: 500px;
    z-index: $stack-below-z-index;
    .blob-profile{
        margin-top: 130px;
        margin-left: 140px;
        width: 200px;
        height: 200px;
        background-color: $darker-bg-white;
        overflow: hidden;
        border-radius: 50%;
        border: 1px solid $dark-red-img-border;
        .me-shape{
            display: inline;
            min-width: 100%;
            margin: 0 auto;
            height: 100%;
            object-fit: cover;
        }
    }

    .normal-blob-ng{
        margin-top: -340px;
        position: absolute;
        width: 500px;
        height: 500px;
        z-index: -1;
    }

    .glow-blob-g{
        margin-top: -340px;
        position: absolute;
        width: 500px;
        height: 500px;
        filter: blur(33px);
        z-index: -2;
    }
}

@mixin default_page_styles_mixin{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    background: $background-col;
}

@mixin default_title_heading_styles_mixin{
    margin-top: 10vh;
    font-size: 42px; 
    text-align: center;
    color: $TT-heading-col;
}