@import "constantMixins";

.landing-page{@include default_page_styles_mixin;}

.NavBar-Header-Menu{
    .open-menu-page{
        position: fixed;
        width: 100vw;
        height: 100vh;
        visibility: hidden;
        background: $blur;
        backdrop-filter: blur(5px);
        z-index: 6000;
        .nav-bar-text{
            position: fixed;
            bottom: 0;
            width: 280px;
            left: 50%;
            transform: translate(-50%, 0%);
            font-size: $font-size-smallest;
            margin-bottom: $margin-val + 70px;
            color: $white-text-col;
            z-index: 3000;
        }
    
        .nav-bar-icons{
            position: fixed;
            margin-bottom: $margin-val + 2px;
            margin-left: $margin-val;
            height: 50px;
            width: 95vw;
            bottom: 0;
            background: $background-col;
            box-shadow: $card-box-shadow;
            border-radius: 18px;
            z-index: 3000;
            display: flex;
            align-items: center;
            justify-content: center;
            .nav-bar-icons-shape{margin-left: 40px;}
        }
    }
    .header-navbar{
        position: fixed;
        z-index: $stack-ontop-z-index;
        display: flex;
        width: 100vw;
        height: 98px;
    
        .glass-bar{
            display: flex;
            margin-top: $margin-val;
            margin-left: $margin-val;
            width: 95vw;
            height: 50px;
            background-color: $light-white-blur-25percent;
            border-radius: 13px;
            align-items: center;
            box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.07);
            backdrop-filter: blur(50px);

            h1{
                color: $light-red-text;
                font-size: $font-size-small;
                margin-left: 1vw;
                cursor: pointer;
            }

            h1:hover{color: $dark-red;}

            .website-links{
                display: flex;
                align-items: center;
                h2, h3{
                    color: $light-gray-text;
                    font-size: $font-size-smallest;
                    font-weight: 600;
                    margin-left: 1.5vw;
                    cursor: pointer;
                }
                h2{margin-left: 30vw;}
                h2:hover, h3:hover{color: $dark-red;}
            }
        }
    
        .darkmode-lightmode-box{
            cursor: pointer;
            .squricle-shape{@include squircle_mixin;}
            .moon-shape{@include squircle_icon_mixin;}
        }
    }
}

.main-content{
    z-index: $stack-below-z-index;
    height: 60vh;
    
    display: flex;
    align-items: center;
    .quick-contact-section{
        width: 13vw;
        min-width: calc(50px + $icon-margin-sep);

        .QC_one{
            margin-top: 30vh;
            margin-left: $icon-margin-sep;
            .squricle-shape{@include squircle_mixin;}
            .gmail-shape{@include squircle_icon_mixin;}
        }

        .QC_two{
            margin-top: $icon-margin-sep; margin-left: $icon-margin-sep;
            .squricle-shape{@include squircle_mixin;}
            .github-shape{@include squircle_icon_mixin;}
        }

        .QC_three{
            margin-top: $icon-margin-sep; margin-left: $icon-margin-sep;
            .squricle-shape{@include squircle_mixin;}
            .call-shape{@include squircle_icon_mixin;}
        }

        .QC_one,.QC_two,.QC_three{cursor: pointer;}
    }

    .blob-objects{
        @include blob_objects_mixin; 
        margin-top: 30vh; 
        margin-left: 19vw;
    }

    .directionalNode{
        cursor: pointer;
        position: fixed;
        margin-top: 30vh;
        margin-left: 98vw;
        .landing-page-dir{
            width: 10px;
            margin-top: 28px;
            height: 10px;
            border-radius: 50%;
            background-color: $light-red-selector;
        }

        .selected-dir{background-color: $dark-red-selector;}
    }
}

.intro-cards{
    display: flex;
    z-index: $stack-below-z-index;
    align-items: center;
    justify-content: center;
    height: 40vh;
    .intro-card-one, .intro-card-two{
        margin-top: 10vh;
        width: 272px;
        height: 100px;
        box-shadow: $card-box-shadow;
        border-radius: 18px;
        background-color: $darker-bg-white;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 600;
        h1{font-size: $font-size-medium; font-weight: 700;color: $white-text-col;}
        h2{font-size: $font-size-small; font-weight: 600;color: $white-text-col;}
        .wavesvg, .laptopsvg{margin-left: 20px;}
    }
    .intro-card-one{margin-right: 4vw;}
    .intro-card-two{margin-left: 4vw;}
}

@media only screen and (min-width: 914px) and (max-width: 1200px){
    .main-content{
        .blob-objects{margin-left: 15vw;}
    }
}

//tablets and medium devices

@media only screen and (min-width: 600px) and (max-width: 914px){
    .NavBar-Header-Menu{
        .open-menu-page{
            .nav-bar-icons{left: 50%;   transform: translate(-50%, 0%);    width: 414px;}
        }
        .header-navbar{
            height: 68px;
            bottom: 0;
        
            .glass-bar{
                width: 91vw;
                .menu-shape{margin-left: 76vw;}
            }
        }
    }

    .main-content{
        .quick-contact-section{ width: 23vw;}

        .blob-objects{margin-left: 0vw;}
    }
}

@media only screen and (min-width: 600px) and (max-width: 780px){
    .main-content{
        .blob-objects{
            width:500px;
            height: 500px;
            margin-left: 0vw;
            .blob-profile{
                margin-top: 75px;
                margin-left: 142px;
            }
            .normal-blob-ng{
                width: 500px;
                height:500px;
            }

            .glow-blob-g{
                width: 500px;
                height: 500px;
            }
        }
    }

    .intro-cards{
        .intro-card-one, .intro-card-two{
            margin-top: -5vh;
        }
    }
}

@media only screen and (min-width: 600px) and (max-width: 713px){
    .NavBar-Header-Menu{
        .header-navbar{
            .glass-bar{
                width: 90vw;
                .menu-shape{margin-left: 75vw;}
            }
        }
    }
    
    .main-content{
        .quick-contact-section{ width: 20vw;}
        .blob-objects{
            margin-top: 30vh;
            width: 500px;
            height: 500px;
            margin-left: 0vw;

            .blob-profile{
                width: 250px;
                height: 250px;
                margin-top: 50px;
                margin-left: 130px;
            }
            .normal-blob-ng{
                margin-top: -360px;
                width: 500px;
                height: 500px;
            }

            .glow-blob-g{
                margin-top: -360px;
                width: 500px;
                height: 500px;
            }
        }
    }

    .intro-cards{
        .intro-card-one, .intro-card-two{
            margin-top: -8vh;
        }
    }
}

//small devices

@media only screen and (max-width: 600px){
    .NavBar-Header-Menu{
        .header-navbar{
            height: 68px;
            bottom: 0;
        
            .glass-bar{
                width: 81.5vw;
                .menu-shape{margin-left: 55.5vw;}
            }
        }
    }

    .main-content{
        .quick-contact-section{
            .QC_one{margin-top: 15vh;margin-left: $margin-val;}
    
            .QC_two{margin-left: $margin-val;}
    
            .QC_three{margin-left: $margin-val;}
        }
        .blob-objects{
            width: 300px;
            height:300px;
            margin-left: 0vw;
            .blob-profile{
                width: 150px;
                height: 150px;
                margin-top: -20px;
                margin-left: 70px;
            }
            .normal-blob-ng{
                margin-top: -220px;
                width: 300px;
                height: 300px;
            }
            .glow-blob-g{
                margin-top: -220px;
                width: 300px;
                height: 300px;
            }
        }
    }

    .intro-cards{
        flex-direction: column;
        .intro-card-one{margin-top: -19vh;margin-right: 0vw;}
        .intro-card-two{margin-top: 4vh;margin-left: 0vw;}
    }
}

@media only screen and (max-width: 600px) and (max-height: 750px){
    .main-content{
        .quick-contact-section{
            .QC_one{margin-top: 10vh;}
        }
    }
}