@import "constantMixins";

.about-page{
    padding-top: 1px;
    margin-top: -1px;
    @include default_page_styles_mixin;

    h1{@include default_title_heading_styles_mixin;margin-top: 14vh;}

    .about-page-main-content{
        display: flex;
        justify-content: center;
        .blob-objects{
            @include blob_objects_mixin;
            margin-top: 1vh; 
            margin-right: $TabMob-margin; 
            margin-left: 0px - $TabMob-margin - $TabMob-margin - $TabMob-margin - $TabMob-margin - $TabMob-margin - $TabMob-margin;
        }

        .bio-desc{
            margin-left: $TabMob-margin;
            margin-top: 6vh;
            border-radius: 23px;
            box-shadow: $large-card-box-shadow;
            background-color: $darker-bg-white;
            width: 321px;
            height: 401px;
            display: flex;
            flex-direction: column; 
            flex-wrap: wrap;        
            justify-content: center;
            align-items: center;
            color: $white-text-col;

            h2{text-align: center; margin-top: 5px;}

            p{
                text-align: justify; 
                width: 307px;
                font-size: 16px;
                font-weight: 500;
                margin-top: 0;
                color: $white-text-col;
            }

            .about-link-btn{
                width: 307px;
                height: 48px;
                background-color: #BFA8FF;
                border-radius: 18px;
                margin-top: 86px;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;
                h3{
                    margin-right: 2px; 
                    color: $btn-colours;
                    font-size: 18px;
                    font-weight: 400;
                }

                .download-cv-lnk{margin-left: 2px;}
            }
        }
    }
}

.Footer-page{
    padding-top: 1px;
    margin-top: -1px;
    @include default_page_styles_mixin;
    height: 65vh;
    display: flex;
    align-items: flex-end;
    justify-content: center;

    .waves-obj{
        width: 100vw;
        margin-bottom: -28vh;
    }
}

@media only screen and (min-width: 600px) and (max-width: 914px){
    .about-page{
        h1{margin-top: 10vh;}

        .about-page-main-content{
            flex-direction: column;
            justify-content: center;
            .blob-objects{
                margin-left: auto;
                margin-right: auto;
            }

            .bio-desc{
                margin-left: auto;
                margin-right: auto;
                margin-top: 3vh;
            }
        }
    }

    .Footer-page{
        height: 34vh;
    
        .waves-obj{margin-top: 0vh; margin-bottom: 0vh;}
    }
}

@media only screen and (min-width: 600px) and (max-width: 780px){
    .about-page{
        height: 115vh;
        .about-page-main-content{
            .blob-objects{
                width: 500px;
                height: 500px;

                .blob-profile{
                    margin-top: 100px;
                    margin-left: 150px;
                }
                .normal-blob-ng{
                    width: 500px;
                    height: 500px;
                }

                .glow-blob-g{
                    width: 500px;
                    height: 500px;
                }
            }
        }
    }
}

@media only screen and (min-width: 600px) and (max-width: 713px){
    .about-page{
        .about-page-main-content{
            .blob-objects{
                width: 500px;
                height: 500px;
                .blob-profile{
                    width: 250px;
                    height: 250px;
                    margin-top: 100px;
                    margin-left: 130px;
                }
                .normal-blob-ng{
                    margin-top: -370px;
                    width: 500px;
                    height: 500px;
                }

                .glow-blob-g{
                    margin-top: -370px;
                    width: 500px;
                    height: 500px;
                }
            }
        }
    }
}

//small devices

@media only screen and (max-width: 600px){
    .about-page{
        height: 105vh;
        h1{margin-top: 10vh;}
        .about-page-main-content{
            flex-direction: column;
            justify-content: center;

            .blob-objects{
                width: 300px;
                height: 300px;
                margin-left: auto;
                margin-right: auto;
                margin-top: 10vh;
                .blob-profile{
                    width: 150px;
                    height: 150px;
                    margin-top: -20px;
                    margin-left: 70px;
                }
                .normal-blob-ng{
                    margin-top: -220px;
                    width: 300px;
                    height: 300px;
                }
                .glow-blob-g{
                    margin-top: -220px;
                    width: 300px;
                    height: 300px;
                }
            }

            .bio-desc{
                margin-left: auto;
                margin-right: auto;
                margin-top: -8vh;
            }
        }
    }

    .Footer-page{
        height: 23vh;
    
        .waves-obj{margin-top: 0vh; margin-bottom: 0vh;}
    }
}

@media only screen and (max-width: 600px) and (max-height: 900px){
    .about-page{
        height: 110vh;
    }
}

@media only screen and (max-width: 600px) and (max-height: 830px){
    .about-page{
        height: 115vh;
    }
}

@media only screen and (max-width: 600px) and (max-height: 750px){
    .about-page{
        height: 140vh;
        .about-page-main-content{
            .bio-desc{
                height: 408px;

                .about-link-btn{margin-top: 93px;}
            }
        }
    }

    .Footer-page{
        height: 26vh;
    
        .waves-obj{margin-top: 0vh; margin-bottom: 0vh;}
    }
}